[class*='zolo-block'] {
    .swiper:not(.swiper-initialized) {
        opacity: 0;
        visibility: hidden;
    }
}

:root {
    --zoloblocks-brand-color: #3f8efc;
    --zoloblocks-brand-color-hover: #2667ff;
    --zoloblocks-brand-text-color: #3f8efc;
    --zoloblocks-brand-button-color: #ffffff;
    --zoloblocks-button-border-radius: 6px;
    --zolo-item-border-radius: 10px;
    --zoloblocks-item-bg-color: rgba(251, 253, 255, 1);
    --zoloblocks-item-border-color: rgba(12, 11, 12, 0.07);
    --zolo-item-border-width: 1px;
    --zolo-item-border-style: solid;
    --zoloblocks-item-box-shadow: '';

    // background
    --zoloblocks-bg-color: '';
    --zoloblocks-bg-gradient: '';
    --zoloblocks-bg-image: '';
    --zoloblocks-bg-repeat: '';
    --zoloblocks-bg-size: '';
    --zoloblocks-bg-attachment: '';
    --zoloblocks-bg-position: '';
}

.zolo-appender-btn {
    margin-top: 30px;
    color: #000;
    background-color: rgba(180, 187, 197, 0.36);
    border-radius: var(--zoloblocks-button-border-radius);
    padding: 12px 15px;
    text-transform: capitalize;
    border: 0;
    font-size: 15px;
    display: flex;
    align-items: center;
    gap: 2px;
    cursor: pointer;
    svg {
        width: 24px;
        height: 24px;
        display: block;
    }
    &:hover {
        background-color: rgba(180, 187, 197, 0.5);
    }
}

// Responsive show/hide css
@media screen and (min-width: 1025px) {
    .zolo-frontend {
        .zolo-hide-desktop {
            display: none !important;
        }
    }
}

// tablet view
@media screen and (max-width: 1024px) and (min-width: 768px) {
    .zolo-frontend {
        .zolo-hide-tab {
            display: none !important;
        }
    }
}

// mobile view
@media screen and (max-width: 767px) {
    .zolo-frontend {
        .zolo-hide-mobile {
            display: none !important;
        }
    }
}

// Responsive show/hide css for Editor
.zolo-editor .zolo-hide-desktop {
    opacity: 0.5;
    background: repeating-linear-gradient(125deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05) 1px, transparent 2px, transparent 9px);
    border: 1px solid rgba(0, 0, 0, 0.02);
}

button[class*='editor-block-list-item-zolo'] {
    // &:button {
    &:before {
        position: absolute;
        content: url('data:image/svg+xml;base64,');
        top: 0;
        right: 0;
        margin: 10px;
        font-size: 9px;
        height: 15px;
        width: 15px;
        opacity: 0.4;
        background-color: #ddd;
        border-radius: 20px;
        padding: 2px;
    }
    &:hover {
        color: var(--zoloblocks-brand-color);
        &:before {
            opacity: 0.8;
            background-color: #aea6a6;
        }
        .block-editor-block-types-list__item-title span {
            color: var(--zoloblocks-brand-color);
        }
        .block-editor-block-types-list__item-icon svg path {
            fill: var(--zoloblocks-brand-color);
        }
    }
}

.block-editor-block-icon:has(.zolo-e-icon) {
    path {
        fill: #2667ff;
    }
}

.components-autocomplete__popover,
.block-editor-block-inspector,
.block-editor-block-toolbar {
    .block-editor-block-icon:has(.zolo-e-icon) {
        position: relative;
        path {
            fill: currentColor;
        }
    }
    .block-editor-block-icon:has(.zolo-e-icon):before {
        background-image: url(data:image/svg+xml;base64,);
        background-size: 100%;
        content: '';
        left: 14px;
        position: absolute;
        top: -5px;
        z-index: 1;
        border: 1px solid #494a51;
        border-radius: 50%;
        padding: 5px;
    }
}
.block-editor-block-inspector,
.block-editor-block-toolbar {
    .block-editor-block-icon:has(.zolo-e-icon) {
        opacity: 0.8;
        path {
            fill: #2667ff;
        }
    }
}

.components-autocomplete__popover {
    .zolo-e-icon {
        width: 20px;
        height: 20px;
    }
}
/**
 * Zolo Blocks Dropdown Controls Style
 */

.zolo-dropdown-popover-content {
    .components-popover__content {
        width: 250px;
    }
}

// transform pro
span.zolo-pro-badge {
    padding: 5.5px;
    display: inline-block;
    align-items: center;
    justify-content: center;
    font-size: 7px;
    line-height: 1;
    color: #fff;
    border-radius: 3px;
    text-transform: uppercase;
    font-weight: 700;
    background: red;
    margin-left: 10px;
}
.zolo-transform-animation {
    --zolo-transform-perspective: 1000px;
    transform: perspective(var(--zolo-transform-perspective, 0)) rotate(var(--zolo-transform-rotate, 0))
        rotateX(var(--zolo-transform-rotateX, 0)) rotateY(var(--zolo-transform-rotateY, 0)) translate(var(--zolo-transform-translate, 0))
        translateX(var(--zolo-transform-translateX, 0)) translateY(var(--zolo-transform-translateY, 0))
        scaleX(calc(var(--zolo-transform-flipX, 1) * var(--zolo-transform-scaleX, var(--zolo-transform-scale, 1))))
        scaleY(calc(var(--zolo-transform-flipY, 1) * var(--zolo-transform-scaleY, var(--zolo-transform-scale, 1))))
        skewX(var(--zolo-transform-skewX, 0)) skewY(var(--zolo-transform-skewY, 0));
    transform-origin: var(--zolo-transform-originY) var(--zolo-transform-originX);
}

.zolo-editor .zolo-block:not(.zolo-entrance-animation) {
    transition: transform 0.3s ease-out;
}

.block-editor-block-breadcrumb li {
    align-items: center;
}

.block-editor-block-breadcrumb__separator {
    width: 30px;
    height: 30px;
}

.block-editor-block-breadcrumb__button.components-button,
.block-editor-block-breadcrumb__current {
    font-size: 14px !important;
    font-weight: 600;
    text-decoration: none;
}
.block-editor-block-breadcrumb__button.components-button:hover {
    background-color: transparent !important;
    color: var(--zoloblocks-brand-color);
}

.block-editor-block-breadcrumb__current {
    color: var(--zoloblocks-brand-color) !important;
}

// Editor Formatting Popover
.components-popover.zolo-format-popover .components-popover__content {
    width: 250px;
    padding: 15px;
}

td.block-editor-list-view-block__contents-cell[aria-selected='true'] {
    svg {
        fill: #fff !important;
        path {
            fill: #fff !important;
        }
    }
}

// sidebar opener styles
.zolo-block {
    position: relative;
    .zolo-blocks-toolbar {
        position: absolute !important;
        display: inline-flex;
        border: none;
        background: #4e62ed;
        cursor: pointer;
        align-items: center;
        justify-content: center;
        z-index: 9;
        opacity: 0;
        visibility: hidden;
        button {
            padding: 5px;
            background: transparent;
            border: 0;
            cursor: pointer;
            svg {
                display: block;
                width: 15px;
                height: 15px;
            }
            &:hover,
            &:focus {
                background: rgba(209, 209, 209, 0.173);
            }
        }

        &.container {
            top: -31px;
            left: 50%;
            width: 109px !important;
            transform: translateX(-50%);
            border-start-start-radius: 5px;
            border-start-end-radius: 5px;
            border-end-start-radius: 0;
            border-end-end-radius: 0;
            svg {
                width: 20px;
                height: 20px;
            }
            button {
                padding: 5px 8px;
            }
        }

        &.general-block {
            top: 0;
            right: 0 !important;
            border-radius: 0;
            border-bottom-left-radius: 6px;
            background-color: #837e7e;
            button {
                border-radius: 0;
                border-bottom-left-radius: 6px;
            }
        }
    }

    .zolo-container-inner-blocks-wrap,
    .block-editor-inner-blocks {
        .zolo-blocks-toolbar {
            &.container {
                transform: unset;
                top: 0;
                left: 0;
                right: unset !important;
                width: auto !important;
                height: auto !important;
                padding: 0px;
                border-radius: 0px;
                border-bottom-right-radius: 6px;
                background-color: #837e7e;
                button {
                    padding: 5px;
                    border-bottom-right-radius: 6px;
                    svg {
                        width: 15px;
                        height: 15px;
                        path {
                            fill: #fff;
                        }
                    }
                }
            }
        }

        &.general-block {
            top: 0 !important;
            right: 0 !important;
            left: unset !important;
            border-radius: 0;
            border-bottom-left-radius: 6px !important;
            background-color: #837e7e !important;
            button {
                border-radius: 0;
                border-bottom-left-radius: 6px;
            }
        }
        .zolo-remove-block,
        .zolo-insert-before {
            display: none;
        }
    }
}

.zolo-block div[class*='wp-block-zolo'] {
    &:hover,
    &:focus {
        .general-block {
            opacity: 1;
            visibility: visible;
        }
    }
}

.zolo-block div[class*='wp-block-zolo'] {
    &:hover,
    &:focus {
        .container {
            opacity: 1;
            visibility: visible;
        }
    }
}

.zolo-block.wp-block-zolo-container {
    &:hover,
    &:focus {
        > div {
            opacity: 1;
            visibility: visible;
        }
    }
}

.block-editor-block-list__layout .block-editor-block-list__block.is-highlighted:after,
.block-editor-block-list__layout .block-editor-block-list__block.is-highlighted ~ .is-multi-selected:after,
.block-editor-block-list__layout .block-editor-block-list__block:not([contenteditable='true']):after,
.block-editor-block-list__layout.is-navigate-mode .block-editor-block-list__block.is-selected:after {
    box-shadow: none !important;
}

.backend.zolo-root-container:hover,
.backend.zolo-root-container.is-selected:focus {
    box-shadow: 0 0 0 1px #4e62ed !important;
}

.block-editor-inner-blocks .zolo-block:hover,
.block-editor-inner-blocks .zolo-block.is-selected:focus {
    box-shadow: 0 0 0 1px #837e7e;
}

.block-editor-block-list__block.zolo-block:hover,
.block-editor-block-list__block.zolo-block.is-selected:focus {
    box-shadow: 0 0 0 1px #837e7e;
}

.zolo-editor {
    .zolo_cd_show,
    .zolo_cd_hide {
        position: relative;
        &:before {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            color: #fff;
            padding: 4px 4px 0px 4px;
            text-transform: capitalize;
            background-color: #3b67fe;
            border-bottom-right-radius: 10px;
            opacity: 0;
            visibility: hidden;
        }
        &:hover,
        &:focus {
            &:before {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    .zolo_cd_show {
        &:before {
            content: url('data:image/svg+xml,%3Csvg%20aria-hidden%3D%22true%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2220%22%20height%3D%2220%22%20fill%3D%22none%22%20color%3D%22%23fff%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20stroke%3D%22currentColor%22%20strokeWidth%3D%221.5%22%20d%3D%22M21%2012c0%201.2-4.03%206-9%206s-9-4.8-9-6c0-1.2%204.03-6%209-6s9%204.8%209%206Z%22%2F%3E%3Cpath%20stroke%3D%22currentColor%22%20strokeWidth%3D%221.5%22%20d%3D%22M15%2012a3%203%200%201%201-6%200%203%203%200%200%201%206%200Z%22%2F%3E%3C%2Fsvg%3E');
        }
    }
    .zolo_cd_hide {
        &:before {
            content: url('data:image/svg+xml,%3Csvg%20aria-hidden%3D%22true%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20display%3D%22block%22%20width%3D%2220%22%20height%3D%2220%22%20fill%3D%22none%22%20color%3D%22%23fff%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20stroke%3D%22currentColor%22%20strokeLinecap%3D%22round%22%20strokeLinejoin%3D%22round%22%20strokeWidth%3D%221.5%22%20d%3D%22M3.933%2013.909A4.357%204.357%200%200%201%203%2012c0-1%204-6%209-6m7.6%203.8A5.068%205.068%200%200%201%2021%2012c0%201-3%206-9%206-.314%200-.62-.014-.918-.04M5%2019%2019%205m-4%207a3%203%200%201%201-6%200%203%203%200%200%201%206%200Z%22%2F%3E%3C%2Fsvg%3E');
        }
    }

    .components-dropdown.block-editor-inserter {
        display: inline;
    }
}

.zolo-frontend .pin-spacer {
    width: 100% !important;
    max-width: 100% !important;
}

.components-dropdown.block-editor-inserter {
