//extension shape divider

.zolo-shape {
    overflow: hidden;
    position: absolute;
    left: 0;
    width: 100%;
    line-height: 0;
    direction: ltr;
    &.zolo-shape-top {
        top: -1px;
    }
    &.zolo-shape-bottom {
        bottom: -1px;
        transform: rotate(180deg);
    }
    svg {
        display: block;
        width: calc(100% + 1.3px);
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        .zolo-shape-fill {
            transform-origin: center;
            transform: rotateY(0deg);
        }
    }
}

.zolo-custom-help-note {
    font-style: italic;
    color: rgb(117, 117, 117);
}

.zolo-editor {
    .zolo-navigation-variation-picker {
        position: relative;
        width: 100%;
        box-sizing: border-box;

        .zolo-navigation-variation-picker-back {
            position: absolute;
            right: 20px;
            top: 20px;
        }
    }

    .zolo-navigation-placeholder {
        position: relative;
        .zolo-navigation-placeholder-skip-button {
            position: absolute;
            right: 20px;
            top: 20px;
        }
    }
}
.zolo-frontend {
    &.ast-single-post {
        .entry-content a,
        .ast-comment-content a:not(.ast-comment-edit-reply-wrap a) {
            text-decoration: unset;
        }
    }
}

.zolo-editor {
    .editor-styles-wrapper .is-root-container a {
        text-decoration: unset;
    }
}

// post title animation type css start here

.zolo-post-title-type-1,
.zolo-post-title-type-2,
.zolo-post-title-type-3,
.zolo-post-title-type-4,
.zolo-post-title-type-5 {
    .zolo-post-title {
        a {
            background-repeat: no-repeat;
            transition:
                background-size 0.3s,
                color 0.2s !important;
            display: inline;

            background-position: 0 100%;
            background-size: 0 100%;
            div {
                display: inline;
            }
        }
    }
}

.zolo-post-title-type-1 {
    --zolo-post-title-type-primary-color: #000;
    .zolo-post-title {
        a {
            background-image: linear-gradient(var(--zolo-post-title-type-primary-color), var(--zolo-post-title-type-primary-color));
            background-position: 0 100%;
            background-size: 0 100%;
            &:hover {
                background-size: 100% 100%;
                color: #fff;
            }
        }
    }
}

.zolo-post-title-type-2,
.zolo-post-title-type-3,
.zolo-post-title-type-4,
.zolo-post-title-type-5 {
    .zolo-post-title {
        a {
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0% 100%;
            background-size: 0% 2px;
            &:hover {
                background-size: 100% 2px;
            }
        }
    }
}

.zolo-post-title-type-3 {
    .zolo-post-title {
        a {
            background-position: 50% 100%;
        }
    }
}

.zolo-post-title-type-4 {
    .zolo-post-title {
        a {
            background-position: 0% 0;
        }
    }
}

.zolo-post-title-type-5 {
    .zolo-post-title {
        a {
            background-position: 50% 0%;
        }
    }
}
// post title animation type css end here

